
import { onMounted, ref } from 'vue';
// import { ShowOnScroll } from '@/functions/show-on-scroll';
import { store } from '@/store';
import { useRoute, useRouter } from 'vue-router';

export default {
  props: {
    galleryIndex: Number,
    imageSourceFull: String,
    imageSourcePreview: String,
    imageSourceRatio: Number,
  },
  setup(props) {
    const route = useRoute();
    const router = useRouter();

    const imageLoader = ref<Element | null>();
    const imageDisplayContainer = ref<Element | null>();
    const imageLoadState = ref<'notLoaded' | 'previewLoaded' | 'fullLoaded'>('notLoaded');

    const image = new Image();
    image.src = props.imageSourcePreview!;
    image.onload = () => {
      imageLoadState.value = 'previewLoaded';

      const fullImage = new Image();
      fullImage.src = props.imageSourceFull!;
      fullImage.onload = () => {
        imageLoadState.value = 'fullLoaded';
      };
    };

    function onImageClick() {
      store.state.portfolioView.currentGalleryIndex = props.galleryIndex!;
      store.state.navigationBar.isVisible = true;
      store.state.navigationBar.transparent = false;
      console.log(store.state.portfolioView.currentGalleryIndex);

      router.push({ name: route.name!, query: { ...route.query, index: props.galleryIndex } });
    }
    // onMounted(() => {
    //   new ShowOnScroll({
    //     selector: '.gallery-image-wrapper',
    //     visibleClassName: 'show-on-scroll-visible',
    //     // applyOnChildElemetSelector: "a",
    //   });
    // });

    onMounted(async () => {
      // let height = image.naturalHeight;
      // let width = image.naturalWidth;
      // //   console.log(height);
      // if (!width || !height) {
      //   await new Promise((resolve) => {
      //     let poll = setInterval(() => {
      //       if (image.naturalHeight) {
      //         console.log(image.naturalHeight);
      //         height = image.naturalHeight;
      //         width = image.naturalWidth;
      //         clearInterval(poll);
      //         resolve(null);
      //         return;
      //       }
      //     }, 10);
      //   });
      // }
      // const aspectRatio = width / height;
      const containerWidth = imageDisplayContainer.value?.getBoundingClientRect().width ?? 300;
      //   console.log(imageDisplayContainer.value);
      (imageLoader.value as HTMLElement).style.height = `${
        containerWidth / props.imageSourceRatio! ?? containerWidth
      }px`;
    });

    return {
      imageLoadState,
      imageDisplayContainer,
      imageLoader,
      onImageClick,
    };
  },
};
